import React from "react"
import { Link } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"
import Layout from "../../components/layout"
import Seo from "../../components/seo"
import "../../styles/main-article-case-study.css"

export default function Home() {
  const description = `神奈川のIT相談ならホットソウルにお任せください！当社がデジタル化やIT活用でサポートさせていただいた実績をご紹介します。 | 2021年7月 GO社会保険労務士法人様 「IT環境 総合診断」`
  const pageUrl = `https://www.hotsoul.jp/case-study/kanno-sharoshi`
  const imageUrl = `https://www.hotsoul.jp/ogp_kanno-sharoshi.jpg`

  return (
    <Layout>
      <Seo title="サポート実績" description={description} imageUrl={imageUrl} pageUrl={pageUrl} />
      <div className="wrap">

        <section className="mv_ac-cs">
          <div className="mv_ac-cs-inner">
            <p className="txt1"><span>サポート実績</span></p>
            <p className="txt2">当社がデジタル化や<br className="sp"/>IT活用で<br className="pc"/>サポートさせていただいた<br className="sp"/>実績をご紹介します。
            </p>
          </div>
        </section>

        <section className="ac-cs">
          <h2>IT環境&nbsp;総合診断</h2>
          <div className="ac-cs-inner">
            <div className="flex">
              <div className="flex-item left">
                <StaticImage
                  src="../../images/main-case-study/kanno-sharoshi/ph1.jpg"
                  alt="GO社会保険労務士法人 様"
                  placeholder="blurred"
                />
              </div>
              <div className="flex-item right">
                <p className="date">2021.7.21</p>
                <p className="title"><span className="bg-yellow">GO社会保険労務士法人 様<br/><span className="small">（旧 かんの社会保険労務士事務所 様）</span></span></p>
              </div>
            </div>
            <div className="flex-table-box">
              <div className="flex-table first">
                <div className="flex-table-item left">
                  <p>お悩み</p>
                </div>
                <div className="flex-table-item right">
                  <ul>
                    <li>複数のITツールを導入済みだが、上手く使いこなせていない</li>
                    <li>テレワークの本格導入に向けて、今の状態で問題がないか不安</li>
                    <li>案件ごとにファイルがバラバラ、どこに何があるのかわからない</li>
                  </ul>
                </div>
              </div>
              <div className="flex-table">
                <div className="flex-table-item left">
                  <p>サポート</p>
                </div>
                <div className="flex-table-item right">
                 <ul>
                   <li>現地調査・ヒアリングで業務フローとIT資産を棚卸し、見える化</li>
                   <li>業務とIT資産を、生産性や情報セキュリティ等の観点で分析評価</li>
                   <li>属人化を防ぐファイル管理やITツール利用のルールづくりをご提案</li>
                   <li>今後の事業計画をヒアリングしつつ、今注力すべき課題を明確化</li>
                 </ul>
                </div>
              </div>
              <div className="flex-table">
                <div className="flex-table-item left">
                  <p>結果</p>
                </div>
                <div className="flex-table-item right">
                  <ul>
                    <li>2021年10月より最適化フェーズのご支援を開始</li>
                    <li>顧客情報と対応履歴の一元化と共有について</li>
                    <li>遊休IT資産の有効活用について</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
          <div className="bg-orange ac-cs-cont">
            <div className="ac-cs-inner">
              <div className="item-box">
                <p className="ttl-support">サポートの背景</p>
                <div className="item first">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">まずは自己紹介と御社のご説明をお願いします。</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">菅野さま：</div>
                    <div className="flex-support-item right">GO社会保険労務士法人、代表社員の菅野と申します。厚労省の助成金申請や、従業員さんに関する手続き業務を主にしています。</div>
                  </div>
                </div>
                <div className="item">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">どういう事情でIT活用サポートを受けようと思われましたか？</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">菅野さま：</div>
                    <div className="flex-support-item right">創業から6年目になるのですが、おかげさまで順調に関与先様が増えてきて、それに伴い従業員も増えてきました。組織として運営していく中で、PCの中に顧客情報や手続きの控えデータが溜まっていき、それを効率化するために<span>色々なソフトを入れていった</span>のですが、ITリテラシーや知識が乏しかったので、業者様に勧められるままになっていました。<span>導入したはいいけど、それぞれを使いこなせていない</span>認識がありました。</div>
                  </div>
                </div>
                <div className="item">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">弊社にご相談いただいたきっかけは？</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">菅野さま：</div>
                    <div className="flex-support-item right">異業種交流会で株式会社ホットソウルの代表の若杉様にお会いしたので、「うちのことちょっと診てください」とお願いしました。</div>
                  </div>
                </div>
              </div>
              <StaticImage
                src="../../images/main-case-study/kanno-sharoshi/ph2.jpg"
                alt="サポートの背景"
                placeholder="blurred"
              />
            </div>
          </div>
          <div className="ac-cs-cont">
            <div className="ac-cs-inner">
              <div className="item-box">
                <p className="ttl-support">相談内容とサポート</p>
                <div className="item first">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">ご相談内容は？</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">菅野さま：</div>
                    <div className="flex-support-item right">導入したソフトやネットワークがうまく使いこなせていないことと、<span>テレワーク</span>についてです。今、一人テレワークの従業員がいるのですが、その方とのやりとりで、<span>自分が今やっているやり方が何か問題がないか</span>。今後<span>テレワークの従業員が増えていったときに問題ないか</span>。そういった部分の相談をさせていただきました。</div>
                  </div>
                </div>
                <div className="item">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">その他のお困りごとはありましたか？</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">菅野さま：</div>
                    <div className="flex-support-item right"><span>書類の保存の仕方などの具体的なルールがなかった</span>ので、従業員がそれぞれ感性でファイル名をつけて保存しています。統一性がなく、<span>どこに何があるのかわからない</span>状態が多く見られました。それを率先してやっていたのが、私なんですけど（笑）。</div>
                  </div>
                </div>
                <div className="item">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">使いこなせていないソフト関するサポートやアドバイスは、<br className="pc"/>どのような内容でしたか？</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">菅野さま：</div>
                    <div className="flex-support-item right">使い方を<span>ルール化</span>することで、人によって違うとか、<span>誰かがいないときにわからないということが防げる</span>ので、その辺りの見直しをアドバイスいただきました。また、自分たちで作ったExcelの管理フォーマットで、十分に業務が回っていると評価していただいたので、そこは少し安心しました。</div>
                  </div>
                </div>
                <div className="item">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">テレワークに関するサポートは？</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">菅野さま：</div>
                    <div className="flex-support-item right">以前、テレワークのために<span>サーバーを導入</span>したのですが、<span>ほとんど使っていないのならば売却も視野にいれて検討</span>してもよいのではというお話でした。<span>セキュリティ面</span>は、基本的な部分はできているとのことで、
                      <span>安心しました</span>。ただ、業者様任せになってしまっているので、今後は自分たちのポリシーに従って、こういう設定にしてほしいと言えるようになりたいです。
                    </div>
                  </div>
                </div>
                <div className="item">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">書類の保存のルールに関するサポートは？</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">菅野さま：</div>
                    <div className="flex-support-item right">今までは依頼主の会社様によって、とあるフォルダが、こちらにはあるけれどあちらにはない、といったようにバラバラでした。フォルダがないことによって、この会社様にはこの業務がない、
                      ということを判断しようとしていたのですが、<span>「あれ何でこのフォルダ無いんだ？〇〇さんが入れ忘れた？」といった感じで、後で混乱する</span>原因になっていました。IT業界でよくある方法として、<span>業務プロセスごとなどで番号を付け、どの案件でも順に決まったフォルダ構成で整理する方法</span>などを提案されました。
                      それはとてもわかりやすいなと思いました。
                    </div>
                  </div>
                </div>
              </div>
              <StaticImage
                src="../../images/main-case-study/kanno-sharoshi/ph3.jpg"
                alt="相談内容とサポート"
                placeholder="blurred"
              />
            </div>
          </div>
          <div className="bg-orange ac-cs-cont">
            <div className="ac-cs-inner">
              <div className="item-box">
                <p className="ttl-support">今後の展望</p>
                <div className="item first">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">今後の進め方をお聞かせください。</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">菅野さま：</div>
                    <div className="flex-support-item right">今回、診断していただいて、ご提案いただいた優先課題への対応を前向きに検討して、今持っているITの資産を上手に使った、効率的な事務所運営ができればと思っています。</div>
                  </div>
                </div>
                <div className="item">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">今回、弊社のサポートを受けてみて良かった点をお聞かせください。</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">菅野さま：</div>
                    <div className="flex-support-item right">こういった社内のIT全般のことって、まったく誰に頼んでいいのかわからないので、ご縁あってお会いできて助かりました。<span>どうなっているかってことを、詳しい方に診てもらいたかった</span>んです。その目的が果たせたのが、本当に良かったです。</div>
                  </div>
                </div>
                <div className="item">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">弊社のおすすめポイントがあればお聞かせください。</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">菅野さま：</div>
                    <div className="flex-support-item right">開業して、うまくいって、<span>どのように機器を入れていったらいいか</span>という時に、わからない方もいらっしゃると思います。うちは、どうしようもない状況の一歩手前で相談できたのが本当によかったので、
                      何か頭に「？」が残ったまま進めるのではなく、<span>事前に相談してプランニング</span>してもらうのが良いと思います。実際、うちの場合、買ったけど必要ないものも出てきてしまったので。
                    </div>
                  </div>
                </div>
                <div className="item">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">逆に、改善点や期待することがあれば。</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">菅野さま：</div>
                    <div className="flex-support-item right">どうしても用語が難しいところですかね（笑）。やさしく接していただきましたが。</div>
                  </div>
                </div>
                <div className="item">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">GO社会保険労務士法人様の今後の展望をお聞かせください。</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">菅野さま：</div>
                    <div className="flex-support-item right">うちのゴールとしては、私が関与している会社様の、<span>従業員さんから「この会社で働いていてよかったです」と言ってもらえる</span>ことなんです。会社をサポートする立場で、士業の中では「ヒト・モノ・カネ」とある中の「ヒト」に関する
                      専門家が社労士です。<span>従業員さんが成長することで会社が成長する</span>組織づくりを、助成金を通じて、または、人事評価制度を通じて、従業員さんが伸びていく価値提供をしていきたいです。企業と従業員さんがともに成長できるような、より上質なコミュニティとして
                      事業発展していく組織づくりをサポートしています。理想を持って起業をしたけど、なかなかその理想とは程遠いなという経営者の方は、私がお役に立てるかもしれませんのでGO社労士法人までご相談ください。</div>
                  </div>
                </div>
              </div>
              <StaticImage
                src="../../images/main-case-study/kanno-sharoshi/ph4.jpg"
                alt="今後の展望"
                placeholder="blurred"
              />
			        <div className="flex-share">
                <ul className="sns">
                  <li>
                    <a href="https://twitter.com/share?url=https://www.hotsoul.jp/case-study/kanno-sharoshi" rel="nofollow noopener noreferrer" target="_blank">
                      <StaticImage
                        src="../../images/icon-tw.png"
                        alt="twitterシェア"
                        placeholder="blurred"
                      />
                    </a>
                  </li> 
                  <li>
                    <a href="http://www.facebook.com/share.php?u=https://www.hotsoul.jp/case-study/kanno-sharoshi" rel="nofollow noopener noreferrer" target="_blank">
                      <StaticImage
                        src="../../images/icon-fb.png"
                        alt="facebookシェア"
                        placeholder="blurred"
                      />
                    </a>
                  </li>
                  <li>
                    <a href="https://social-plugins.line.me/lineit/share?url=https://www.hotsoul.jp/case-study/kanno-sharoshi" rel="nofollow noopener noreferrer" target="_blank">
                      <StaticImage
                        src="../../images/icon-line.png"
                        alt="LIMEシェア"
                        placeholder="blurred"
                      />
                    </a>
                  </li>
                </ul>
			        </div>
              <br/>                
              <p className="archive"><Link to={`/case-study/articles`}>他の記事も読む</Link></p>
            </div>
          </div>
        </section>
      </div>
    </Layout>
  )
}